import { Component, OnInit } from '@angular/core';
import { MainServiceService } from '../main-service.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-image-enlarger',
  templateUrl: './image-enlarger.component.html',
  styleUrls: ['./image-enlarger.component.css']
})
export class ImageEnlargerComponent implements OnInit {
  ImageUrl: string;
  ImageInfo: string;

  constructor(private Service : MainServiceService, private Router: Router) { }

  ngOnInit(): void {
    this.ImageUrl=this.Service.URL.Link;
    this.ImageInfo=this.Service.URL.Info;
    if(this.ImageUrl == '')
    {
        this.back();
    }
  }
  back(){
    this.Router.navigate(['/LandingPage']);
  }

}
