basicCarouselSwitch(val){
  if(val == 0 && this.lowValue != 0){
    this.lowValue--;
    this.highValue--;
  }
  else if(val == 1 && this.highValue != 5){
    this.lowValue++;
    this.highValue++;
  }
}

testimonialsSwitch(val){
  if(val == 0 && this.low != 0){
    this.low--;
    this.high--;
  }
  else if(val == 1 && this.high != 5){
    this.low++;
    this.high++;
  }
}
